.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

button {
  font-size: 21px;
  line-height: 26px;
  font-weight: 700;
  background-color: @togglebackground;
  color: #ffffff;
}
